.heading {
  color: var(--Gray-900, #101828);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
}

.border {
  border: 1px solid #f2f4f7 !important;
}

.borderBottom {
  border-bottom: 1px solid #f2f4f7 !important;
}

.cardBody {
  padding: 12px 24px !important;
}

.jobDetailHeading {
  color: var(--Gray-600, #475467);
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: -0.18px;
}

.customLabel {
  color: var(--Text-Gray, #667085);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.14px;
}

.text {
  color: var(--Gray-900, #101828);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.plannedTable {
  border-collapse: collapse;
  width: 100%;
}

.plannedTable th {
  padding: 8px 16px;
  border-bottom: 1px solid var(--Gray-200, #eaecf0);
}

.plannedTable th:nth-child(2),
.plannedTable td:nth-child(2) {
  border-left: 1px solid var(--Gray-200, #eaecf0);
  border-right: 1px solid var(--Gray-200, #eaecf0);
}

.plannedTable td {
  color: var(--Gray-600, #475467);
  padding: 8px 16px;
  border-bottom: 1px solid var(--Gray-200, #eaecf0);
}

.whiteTr td {
  background: #fff !important;
}

.plannedTable td:nth-child(2),
.plannedTable td:nth-child(3) {
  text-align: right;
}

.plannedTable th.active1 {
  background: var(--Primary-50, #ebf8ff);
  border: 1px solid var(--Primary-300, #76d1ff) !important;
  border-left: 1px solid var(--Primary-300, #76d1ff) !important;
}

.plannedTable th.nonActive {
  border-right: unset !important;
}

.plannedTable td.active1 {
  border-right: 1px solid var(--Primary-300, #76d1ff) !important;
  border-left: 1px solid var(--Primary-300, #76d1ff) !important;
}

.plannedTable td.nonActive {
  border-right: unset !important;
}

.plannedTable td.active1.borderRightNone {
  border-right: none !important;
}

.plannedTable td.active1.active2 {
  background: var(--Primary-50, #ebf8ff);
}

.plannedTable td.active3 {
  border-right: 1px solid var(--Primary-300, #76d1ff) !important;
}

.lastRow td {
  border-bottom: none !important;
}

.fixRateBox {
  display: flex;
  padding: 4px 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  background: #e1fc9d;
  color: var(--Gray-800, #1d2939);

  /* Title group */
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px; /* 142.857% */
  letter-spacing: 0.21px;
}

.shiftBox {
  display: flex;
  padding: 4px 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  border: 1px solid var(--Gray-300, #d0d5dd);
  background: #fff;
  color: var(--Gray-800, #1d2939);

  /* Title group */
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px; /* 142.857% */
  letter-spacing: 0.21px;
}

.clockIcon {
  display: flex;
  padding: 6px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  border: 1px solid var(--Gray-300, #d0d5dd);
  background: var(--Gray-25, #fcfcfd);
}

.moneyBox {
  margin: auto;
  width: fit-content;
  color: var(--Gray-700, #344054);
  text-align: center;

  /* Body large */
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  letter-spacing: -0.08px;
  display: flex;
  padding: 2px 6px;
  align-items: center;
  border-radius: 4px;
  border: 1px solid var(--Gray-300, #d0d5dd);
  background: var(--Base-White, #fff);
}

.InputTextarea {
  width: 100% !important;
  border-radius: 4px !important;
  border: 1px solid var(--Gray-300, #d0d5dd) !important;
  background: var(--Base-White, #fff) !important;
  box-shadow: none !important;
  outline: none !important;
  resize: none !important;
}

.activeOutlineButton,
.activeOutlineButton:hover,
.activeOutlineButton:focus {
  color: #fff !important;
  background: var(--Primary-600, #158ecc) !important;
  border: 1px solid var(--Primary-600, #158ecc) !important;
}
